/* Profile */
.about-me {
    display: flex;
    margin: 25px;
    padding: 50px;
    gap: 50px;
    font-family: 'Courier New', Courier, monospace;
    align-items: center;
    justify-content: space-between;
    text-align: justify;
    border-radius: 16px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
    background: linear-gradient(135deg, #00eaffb3, #007cf0b3);
    color: white;
}

.profile-image {
    height: 250px;
    width: 250px;
    border-radius: 50%;
    border: 4px solid #ffffffcc;
    box-shadow: 0 0 15px rgba(0, 255, 255, 0.4);
    /* border: 5px double rgba(195, 205, 205, 0.893); */
}

.profile-image img {
    height: 250px;
    width: 250px;
    border-radius: 50%;
    background: radial-gradient(#00eaffb3, #008d9a7f);
    backdrop-filter: blur(25px);
}

